.contact{
    position: relative;
    padding: 120px 0 90px 0;
    background-color: $bg-color;
    &__wrapper{
        padding: 0 35px 0 52px;
        display: grid;
        grid-template-columns: 400px 585px;
        justify-content: space-between;
    }
    &__photo{
        img{
            width: 100%;
        }
    }
    &__text, &__social{
        margin-top: 20px;
    }
    &__wrapper{
        position: relative;
    }
    
    a{
        margin-right: 25px;
        img{
            max-width: 30px;
            max-height: 30px;
        }
    }
    &__form{
        margin-top: 20px;
        display: grid;
        grid-template:  45px 190px 45px / repeat(2, 280px);
        justify-content: space-between;
        row-gap: 30px;
        label{
            display: block;
            position: absolute;
            height: 18px;
            padding: 0 8px;
            background-color: $bg-color;
            font-size: 12px;
            margin-bottom: 0;
            top: -9px;
            left: 12px;
        }
    }
    &__triggers{
        display: grid;
        grid-column: 1/3;
        grid-template-columns: repeat(2, auto);
        column-gap: 35px;
    }
    &__policy{
        font-size: 12px;
        line-height: 14px;
        display: flex;
        align-items: center;
        input{
            margin-right: 10px;
        }
    }
    &__input{
        position: relative;
        input{
            width: 100%;
            height: 100%;
            padding: 0 20px;
            font-size: 12px;
            font-weight: 500;
        }
        
    }
    &__textarea{
        position: relative;
        grid-column: 1 / 3;
        textarea{
            resize: none;
            width: 100%;
            height: 100%;
            padding: 20px;
            font-size: 12px;
        }
    }
    &__btn{
        border: none;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
    }
}