.price{
    padding-top: 60px;
    background: $bg-color;
    &__wrapper{
        padding-top: 60px;
        display: grid;
        grid-template-columns: repeat(2, 366px);
        justify-content: space-around;
    }
    &__list{
        display: grid;
        grid-template-rows: repeat(3, 44px);
        width: 370px;
        gap: 15px;
    }
    &__block{
        position: relative;
        &-title{
            &::before{
                content: '';
                position: absolute;
                width: 15px;
                height: 15px;
                background-color: $main-color;
                border-radius: 100%;
                left: calc(-10px + -15px);
                top: 2px;

            }
        }
        &-elem{
            display: grid;
            grid-template-columns: 188px 121px;
            justify-content: space-between;
        }
    }
}