@media (max-width: 1200px){
    .container{
        max-width: 960px;
    }
    .sidepanel{
        display: none;
    }
    .eshgin{
        &__block{
            grid-template-columns: 400px 500px;
            gap: 50px;
        }
        &__info{
            grid-template-columns: 520px;
        }
        &__portfolio{
            grid-template-columns: 220px 220px;
        }
    }
    .resume{
        &__wrapper{
            grid-template-columns: repeat(2, 386px);
            justify-content: center;
        }
    }
    .skills{
        padding-top: 80px;
        &__block{
            justify-content: center;
            padding-top: 40px;
            grid-template-columns: repeat(3, 270px);
        }
        &__experience{
            padding-top: 90px;
            justify-content: center;
            grid: 70px 70px/270px 270px 270px;
        }
        &__list{
            grid-template-columns: 250px;
        }
        &__divider{
            width: 100%;
        }
    }
    .portfolio{
        &__wrapper{
            grid-template-columns: repeat(auto-fit, 280px);
            grid-auto-rows: 280px;
            justify-content: space-around;
            row-gap: 20px;
            .horizonal, .vertical{
                grid-column: unset;
                grid-row: unset;
            }
        }
    }
    .contact{
        &__form{
            grid-template: 45px 45px 190px 45px/ repeat(auto-fit, 280px);
            row-gap: 20px;
        }
        &__wrapper{
            grid-template-columns: 400px 453px;
            column-gap: 20px;
        
        }
        &__mail{
            grid-row: 2/3;
            grid-column: 1/2;
        }
        &__textarea{
            grid-row: 3 / 5;
            grid-column: 1/2;
        }
        &__triggers{
            grid-column: 1 / 2;
            grid-template: 1fr 1fr / 280px;
        }
    }
}

@media (max-width: 992px){
    .container{
        max-width: 720px;
    }
    .eshgin{
        &__photo{
            width: 281px;
        }
        &__block{
            grid-template-columns: 281px 375px;
        }
        &__info{
            grid-template-columns: 375px;
        }
        &__elem{
            grid-template-columns: 28px 130px;
        }
        &__portfolio{
            grid-template-columns: 190px 162px;
        }
    }
    .resume{
        &__wrapper{
            grid-template-columns: repeat(2, 330px);
            column-gap: 43px;
        }
    }
    .skills{
        &__block{
            justify-content: center;
            padding-top: 40px;
            grid-template-columns: repeat(2, 270px);
        }
        &__experience{
            padding-top: 60px;
            justify-content: center;
            grid: 70px 70px/270px 270px;
            gap: 35px;
        }
    }
    .price{
        &__wrapper{
            padding: 30px 0 0 40px;
           
        }
        &__list{
            width: 330px;
        }
    }
    .contact{
        padding-top: 40px;
        &__photo{
            display: none;
        }
        &__wrapper{
            grid-template-columns: 1fr;
        }
        &__form{
            column-gap: 20px;
            justify-content: unset;
            grid-template: 45px 190px 45px/ repeat(2, 280px);
        }
        &__mail{
            grid-column: unset;
            grid-row: unset;
        }
        &__textarea{
            grid-column: 1 / 3;
            grid-row: unset;
        }
        &__triggers{
            grid-template-columns: repeat(2, 1fr);
            grid-column: 1/3;
        }
        &__social{
            display: flex;
            justify-content: center;
        }
        .title{
            text-align: center;
            &_fz16px{
                text-align: center;
            }
        }
        .divider{
            &_mintop{
                display: block;
                margin: 0 auto;
                margin-bottom: 10px;
            }
        }

    }
}

@media (max-width: 768px){
    .container{
        max-width: 540px;
    }
    .title{
        &_fz48px{
            font-size: 35px;
        }
    }
    .promo{
        &__title{
        line-height: 39px;
        width: 300px;
        }
    }
    .eshgin{
        padding: 20px 0;
        &__block{
            grid-template-columns: 300px;
            justify-content: center;
            gap: 30px;
        }
        &__photo{
            width: 300px;
            height: auto
        }
        &__portfolio{
            justify-content: center;
            grid-template-columns: 200px;
        }
        &__info {
            grid-template-columns: 300px;
        }
        
        .title{
            text-align: center;
            &_fz16px{
                text-align: center;
            }
        }
        .divider{
            
            &_mintop{
                display: block;
                margin: 0 auto;
                margin-bottom: 10px;
            }
        }
        .text{
            text-align: center;
        }
        
    }
    .resume{
        &__wrapper{
            grid-template-columns: repeat(1, 365px);
            column-gap: 43px;
            padding-top: 25px;
        }
    }
    .skills{
        &__block{
            justify-content: center;
            padding-top: 40px;
            grid-template-columns: repeat(2, 250px);
        }
        &__experience{
            padding-top: 60px;
            justify-content: center;
            grid: 70px 70px/250px 250px;
            gap: 35px;
        }
        &__wrapper{
            justify-content: space-around;
        }
    }
    .porttolio{
        padding-top: 40px;
    }
    .price{
        padding-top: 40px;
        &__wrapper{
            margin-top: 20px;
            grid-template-columns: repeat(auto-fit, 350px);
            justify-content: center;
            gap: 21px;
        }
    }
    .contact{
        .title{
            text-align: left;
            &_fz16px{
                text-align: left;
            }
        }
        .divider{
            &_mintop{
                margin: 0;
                margin-top: 15px;
                margin-bottom: 20px;
            }
        }
        .text{
            text-align: left;
        }
        &__social{
            justify-content: unset;
        }
        &__form{
            grid-template: 45px 45px 190px 45px/ 300px;
        }
        &__mail{
            grid-row: 2 / 3;
        }
        &__textarea{
            grid-column: 1 / 2;
        }
        &__triggers{
            grid-template-columns: 300px;
            grid-template-rows: 1fr 1fr;
            row-gap: 20px;
            grid-column: 1/2;
            justify-content: center;
        }
        &__policy{
            grid-row: 1 / 2;
        }
    }
}

@media (max-width: 576px){
    .container{
        max-width: 100%;
        padding: 0 10px;
    }
    .title{
        &_fz48px{
            font-size: 26px;
        }
        
    }
    .hamburger{
        right: 20px;
    }
    .menu{
        &__block{
            width: 250px;
            padding-left: 20px;
        }
    }
    .promo{
        background: url("../img/esh.png") no-repeat 57%/cover;
        &__btns{
            margin-top: 35px;
        }
    }
    
    .eshgin{
        padding-top: 46px;
        &__photo{
            display: none;
        }
        &__portfolio{
            justify-content: left;
        }
        &__elem{
            grid-template-columns: 30px 250px;
        }
        .title{
            text-align: left;
            font-size: 24px;
            line-height: 36px;
            margin-bottom: 15px;
            &_fz16px{
                margin-bottom: 10px;
            }
            &_fz14px{
                font-size: 14px;
                line-height: 21px;
                margin-bottom: 6px;
            }            
        }
        .divider{
            &_mintop{
                margin: 0 0 20px 0;
                
            }
        }
        .text{
            margin: 0;
            font-size: 14px;
            text-align: left;
        }
    }
    .resume{
        .title{
            &__section-title{
                margin-bottom: 26px;
                
            }
            &__section-subtitle{
                font-size: 26px;
                margin-bottom: 15px;
            }
        }
        &__wrapper{
            grid-template-columns: repeat(1, 275px);
            padding: 0px 12px 0 15px;
        }
        &__column-title{
            margin-top: 40px;
        }
    }
    .skills{
        .title{
            &__section-title{
                margin-bottom: 26px;
                
            }
            &__section-subtitle{
                font-size: 26px;
                margin-bottom: 15px;
            }
        }
        &__block{
            justify-content: center;
            padding-top: 20px;
            grid-template-columns: repeat(1, 270px);
        }
        &__experience{
            padding: 40px 10px 0 10px;
            justify-content: center;
            grid: 70px 70px 70px 70px 70px/ 300px;
            gap: 35px;
        }
    }
    .price{
        &__wrapper{
            padding-left: 0;
            grid-template-columns: repeat(auto-fit, 300px);
        }
        &__list{
            width: 290px;
        }
        &__block{
            width: 290px;
        }
        .money{
            font-size: 14px;
        }
    }
    .contact{
        .title{
            &_fz36px{
                font-size: 24px;
            }
        }
        &__wrapper{
            padding: 50px 0px;
        }
    }
}