.skills{
    background-color: $bg-color;
    padding-top: 156px;
    &__block{
        padding: 60px 10px 0 10px;
        display: grid;
        grid-template-columns: repeat(4, 270px);
        grid-template-rows: repeat(2, minmax(270px));
        gap: 13px;
    }
    &__elem{
        padding: 30px 30px 25px 30px;
        border: 1px solid #000;
        &-title{
            margin: 20px 0 15px 0;
            color: #000;
        }
    }
    &__experience{
        padding: 120px 0;
        display: grid;
        grid: 70px 70px / 350px 350px 250px;
        gap: 45px;
    }
    &__list{
        display: grid;
        grid-template-rows: auto auto;
        
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
    }
    &__divider{
        width: 350px;
        height: 14px;
        background: #E8E4DE;
        span{
            display: block;
            height: 100%;
            color: $main-color;
        }
    }
    &__wrapper-title{
        position: relative;
        text-align: center;
        @include before_circle;
        z-index: 2;
        &::before{
            top: -6px;
            left: 0;
            transform: translateX(-50%);
        }
    }
}