.resume{
    background-color: #e4d8d8;
    
    &__wrapper{
        display: grid;
        grid-template-columns: repeat(2, 425px);
        padding: 60px 65px 0 65px;
        column-gap: 135px;
    }
    &__column{
        &-title{
            position: relative;
            padding-left: 35px;
            &:before{
                content: '';
                position: absolute;
                width: 15px;
                height: 15px;
                background: $main-color;
                border-radius: 100%;
                left: -7px;
                top: 50%;
                transform: translateY(-50%);
                
            }
        }
        ul{
            list-style-type: none;
            li{
               position: relative;
               &::after{
                   position: absolute;
                   content: '';
                   width: 1px;
                   height: calc(100% + 16px);
                   top: -16px;
                   left: 0;
                   background-color: #000;
               }  
            }
        }

    }
    &__item{
        position: relative;
        &::before{
            top: 68px;
            content: '';
            height: 1px;
            width: 43px;
            position: absolute;
            background-color: #000;
        }
        &-head{
            position: relative;
            padding: 45px 0 0 100px;
        }
        &-body{
            padding-left: 43px;
            margin-top: 12px;
        }
        &-icon{
            position: absolute;
            top: 55px;
            left: 45px;
            @include before_circle;
            z-index: 3;
            img {
                position: absolute;
                left: 17px;
                top: 14px;
            }
        }
        &-location{
            margin-top: 5px;
            font-size: 12px;
            line-height: 18px;
        }
        &-body{
            font-size: 12px;
            line-height: 18px;
        }
    }
    
}