.portfolio{
    padding-top: 120px;
    background: $bg-color;
    &__wrapper{
        margin-top: 60px;
        display: grid;
        grid-template: repeat(2, 280px) / repeat(4, 280px);
        grid-auto-flow: 280px;
        gap: 5px;
        .vertical{
            grid-row: 1 / 3;
            grid-column: 4 / 5;
        }
        .horizonal{
            grid-row: 2 / 3;
            grid-column: 2 / 4;
        }
    }
    a{
        transition: all 0.6s;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
        &:hover{
            transform: scale(1.03);
        }
    }
}