.title{
    font-weight: bold;
    font-size: 36px;
    color: #ffff;
    &_fz16px{
        font-size: 16px;
        line-height: 24px;
        @include before_circle;
        z-index: 2;
        &::before{
            top: -6px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    
    &_fz48px{
        font-size: 48px;
    }
    &_fz14px{
        font-size: 14px;
        line-height: 21px;
    }
    &_fz20px{
        font-size: 20px;
        line-height: 30px;
    }
    &__section-title{
        position: relative;
        text-align: center;
        @include before_circle;
        z-index: 2;
        &::before{
            top: -6px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    &__section-subtitle{
        text-align: center;
    }
    &_policy{
        text-align: center;
        font-weight: 700;
        color: black;
        font-size: 16px;
    }
}