.promo{
    position: relative;
    min-height: 100vh;
    background: url("../img/esh.png") no-repeat 50%/cover;
    padding-top: 205px;
    
    &__btns{
        margin-top: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 250px;
        
    }
    &__link{
        color: #fff;
        font-weight: bold;
        &:hover{
            color: #000;
            text-decoration: none;
        }
    }
}