.eshgin{
    background-color: rgb(228, 216, 216);
    padding-top: 120px;
    &__block{
        display: grid;
        grid-template-columns: 400px 570px;
        align-items: start;
        gap: 70px;
    }
    &__photo{
        width: 400px;
        height: 600px;
        img{
            width: 100%;
        }
    }
    &__info{
        display: grid;
        grid-template-rows: minmax(100px, auto);
        
    }
    &__biography
        .title{
        position: relative;
        @include before_circle;
    }
    &__portfolio{
        margin-top: 20px;
        display: grid;
        grid-template-columns: 260px 260px;
        grid-template-rows: minmax(116px, auto);
        column-gap: 40px;
        row-gap: 20px;
        
    }
    &__elem{
        display: grid;
        grid-template-columns: 40px 200px;
        gap: 20px;
    }
    &__text{
        margin-top: 10px;
    }
}